
body, html, #app {
  width: 100%;
  height: 100%;
}
body {
  margin: 0;
  padding: 0;
  font-family: 'Roboto';
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  -webkit-font-smoothing: antialiased;
  color: #2c3e50;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  font-size: 12px;
}
ul {
  list-style: none;
  margin: 0;
  padding: 0;
}
input,
textarea {
  outline: none;
}
a {
  text-decoration: none;
}
.editor-plugin .edui-default .edui-editor {
  border: none;
  margin: 0 auto;
}
.file-uploads {
  height: 140px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  background: linear-gradient(45deg, #e7e7e7 25%, #fff 0, #fff 50%, #e7e7e7 0, #e7e7e7 75%, #fff 0);
  background-size: 30px 30px;
}
.help {
  background: #ff7878;
  display: block;
  font-weight: bold;
  color: #fff;
  padding: 0 10px;
}
.is-danger {
  border: 1px solid #ff7878;
}
table, th, td {
  border: 1px solid #c2c2c2;
}
table {
  border-collapse: collapse;
  width: 100%;
}
table tr {
    background: -webkit-gradient(linear, left top, left bottom, color-stop(30%, #f5f5f5), color-stop(90%, #dbdbdb));
    background: linear-gradient(#f5f5f5 30%, #dbdbdb 90%);
}
table td {
    -webkit-box-shadow: -1px -1px 0 #f5f5f5;
            box-shadow: -1px -1px 0 #f5f5f5;
    vertical-align: middle;
    padding: 6px 10px;
}
table th {
    background: -webkit-gradient(linear, left top, left bottom, color-stop(30%, #313950), color-stop(90%, #272a33));
    background: linear-gradient(#313950 30%, #272a33 90%);
    border: 1px solid #3b3b8f;
    padding: 6px 0;
    color: white;
    font-weight: bold;
}
.zpagenav {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  font-size: 14px;
  margin-top: 20px;
}
.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  border: 0;
}
.sr-only-focusable:active, .sr-only-focusable:focus {
  position: static;
  width: auto;
  height: auto;
  margin: 0;
  overflow: visible;
  clip: auto;
}
.m-r-1 {
  margin-right: 1rem !important;
}
.pagination {
  display: inline-block;
  padding-left: 0;
}
.page-item {
  display: inline;
}
.page-item:first-child .page-link {
  margin-left: 0;
}
.page-item.active .page-link, .page-item.active .page-link:focus, .page-item.active .page-link:hover {
  z-index: 2;
  color: #fff;
  cursor: default;
  background: -webkit-gradient(linear, left top, left bottom, color-stop(30%, #313950), color-stop(90%, #272a33));
  background: linear-gradient(#313950 30%, #272a33 90%);
  border: 1px solid #131a2e;
}
.page-item.disabled .page-link, .page-item.disabled .page-link:focus, .page-item.disabled .page-link:hover {
  color: #818a91;
  cursor: not-allowed;
  background-color: #fff;
  border-color: #ddd;
}
.page-link {
  position: relative;
  float: left;
  padding: .5rem .75rem;
  margin-left: 2px;
  line-height: 1.5;
  color: #666;
  text-decoration: none;
  background-color: #fff;
  border-radius: 4px;
  border: 1px solid #d6d6d6;
  background: -webkit-gradient(linear, left top, left bottom, color-stop(30%, #e5e5e5), color-stop(90%, #D6D6D6));
  background: linear-gradient(#e5e5e5 30%, #D6D6D6 90%);
}
.page-link:focus, .page-link:hover {
  color: #014c8c;
  background-color: #eceeef;
  border-color: #ddd;
}
.pagination-lg .page-link {
  padding: .75rem 1.5rem;
  font-size: 1.25rem;
  line-height: 1.333333;
}
.pagination-sm .page-link {
  padding: .275rem .75rem;
  font-size: .875rem;
  line-height: 1.5;
}
.pagination-sm .page-item:first-child .page-link {
  border-top-left-radius: .2rem;
  border-bottom-left-radius: .2rem;
}
.pagination-sm .page-item:last-child .page-link {
  border-top-right-radius: .2rem;
  border-bottom-right-radius: .2rem;
}
.pager {
  padding-left: 0;
  margin-top: 1rem;
  margin-bottom: 1rem;
  text-align: center;
  list-style: none;
}
.pager::after {
  display: table;
  clear: both;
  content: "";
}
.pager li {
  display: inline;
}
.pager li > a,
.pager li > span {
  display: inline-block;
  padding: 5px 14px;
  background-color: #fff;
  border: 1px solid #ddd;
  border-radius: 15px;
}
.pager li > a:focus, .pager li > a:hover {
  text-decoration: none;
  background-color: #eceeef;
}
.pager .disabled > a, .pager .disabled > a:focus, .pager .disabled > a:hover {
  color: #818a91;
  cursor: not-allowed;
  background-color: #fff;
}
.pager .disabled > span {
  color: #818a91;
  cursor: not-allowed;
  background-color: #fff;
}
.pager-next > a,
.pager-next > span {
  float: right;
}
.pager-prev > a,
.pager-prev > span {
  float: left;
}



